html {
  font-size: 16px;
}
body {
  margin: 0;
  font-family: Roboto, Arial, Helvetica, sans-serif;
  color: #475257;
}
::selection {
  color: #fff;
  text-shadow: none;
  background-color: #175e82;
}
.u-hidden {
  display: none !important;
}
.u-hidden--visually {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  white-space: nowrap;
  border: 0;
  clip-path: inset(50%);
}
@media print {
  .u-hidden--print {
    display: none !important;
  }
}
@media screen {
  .u-hidden--screen {
    display: none !important;
  }
}
@media screen and (max-width: 479px) {
  .u-hidden--xs {
    display: none !important;
  }
}
@media screen and (max-width: 639px) {
  .u-hidden--sm {
    display: none !important;
  }
}
@media screen and (max-width: 767px) {
  .u-hidden--md {
    display: none !important;
  }
}
@media screen and (max-width: 1023px) {
  .u-hidden--lg {
    display: none !important;
  }
}
@media screen and (max-width: 1279px) {
  .u-hidden--xl {
    display: none !important;
  }
}
.Resume a {
  color: #0f7cb3;
  text-decoration: none;
  border-bottom: 1px solid transparent;
  transition: border-color 0.1s ease-in;
}
.Resume a:hover {
  border-color: currentColor;
}
.Resume-header {
  align-items: flex-end;
  justify-content: space-between;
  padding: 3rem 0 1.5rem;
}
@media (min-width: 768px) {
  .Resume-header {
    padding-top: 4rem;
    padding-bottom: 2rem;
  }
}
@media print, (min-width: 1024px) {
  .Resume-header {
    display: flex;
  }
}
@media print {
  .Resume-header {
    padding: 0 0 1rem;
  }
}
.Resume-header a {
  color: inherit;
}
.Resume-name {
  margin: 0 0 1rem -3px;
  font: 3em Kanit, Roboto, Arial, Helvetica, sans-serif;
  line-height: 1;
  color: #0f7cb3;
  letter-spacing: 0.03em;
}
@media print, (min-width: 1024px) {
  .Resume-name {
    margin-bottom: -6px;
  }
}
.Resume-name a {
  display: inline-block;
}
.Resume-contactInfo {
  padding: 0 0 5px;
  margin: 0 -1rem -5px;
  overflow: hidden;
  line-height: 1.5;
  list-style-type: none;
}
@media (min-width: 355px) {
  .Resume-contactInfo {
    display: flex;
    line-height: inherit;
  }
}
@media print {
  .Resume-contactInfo {
    color: #475257;
  }
}
.Resume-contactInfoItem {
  padding: 0 calc(1rem + 2px) 0 1rem;
  margin-left: -2px;
  border-left: 1px solid currentColor;
}
.Resume-header,
.Resume-section {
  max-width: 1024px;
  margin: 0 1rem;
}
@media (min-width: 480px) {
  .Resume-header,
  .Resume-section {
    margin-right: 1.5rem;
    margin-left: 1.5rem;
  }
}
@media (min-width: 40em) {
  .Resume-header,
  .Resume-section {
    margin-right: 3rem;
    margin-left: 3rem;
  }
}
@media (min-width: 72em) {
  .Resume-header,
  .Resume-section {
    margin-right: auto;
    margin-left: auto;
  }
}
@media print {
  .Resume-header,
  .Resume-section {
    max-width: none;
    margin: 0;
  }
}
.Resume-section {
  padding: 1rem 0;
}
@media (min-width: 480px) {
  .Resume-section {
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
  }
}
@media print {
  .Resume-section {
    padding-top: 1rem;
    padding-bottom: 1rem;
  }
}
@media print {
  .Resume-section:last-child {
    padding-bottom: 0;
  }
}
.Resume-sectionHeader {
  position: relative;
  border-top: 2px solid #0f7cb3;
}
.Resume-sectionHeading {
  display: inline-block;
  padding: 1px 4px;
  margin-top: 0;
  margin-bottom: 1rem;
  font-size: 0.8125rem;
  font-weight: 700;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 0.15em;
  vertical-align: top;
  background-color: #0f7cb3;
  border: 2px solid #0f7cb3;
}
@media print, (min-width: 48em) {
  .Resume-sectionHeading {
    position: absolute;
    top: 0;
    border-top: 0;
  }
}
@media (min-width: 48em) {
  .Resume-sectionHeading {
    margin-bottom: 0;
  }
}
@media print {
  .Resume-sectionHeading {
    padding-top: 2px;
    color: #0f7cb3;
    background-color: transparent;
  }
}
.Resume-content {
  flex: 1;
  line-height: 1.5;
  color: #475257;
}
@media print, (min-width: 48em) {
  .Resume-content {
    padding-top: 0.75rem;
    padding-left: 8rem;
  }
}
.Resume-content--grid {
  margin: -1rem;
}
@media print, (min-width: 1024px) {
  .Resume-content--grid {
    display: flex;
    flex-wrap: wrap;
  }
}
.Resume-content h2,
.Resume-content h3 {
  margin-top: 0;
  color: #3d525c;
}
.Resume-content h2 {
  margin-bottom: 0;
  font-size: 1.25rem;
  font-weight: 500;
}
.Resume-content h3 {
  margin-bottom: 0.25em;
  font-size: 1.1rem;
  font-weight: 400;
}
.Resume-content p,
.Resume-content ul,
.Resume-content ol {
  margin: 0;
}
.Resume-content p + p {
  margin-top: 1em;
}
.Resume-content ul {
  padding-left: 18px;
  list-style-type: circle;
}
.Resume-content li {
  margin-bottom: 0.5em;
}
@media print {
  .Resume-content a::after {
    font-size: 0.75em;
    content: " (" attr(data-short-url) ")";
  }
}
@media print, (min-width: 1024px) {
  .Resume-row {
    display: flex;
  }
}
.Resume-row + .Resume-row {
  margin-top: 1.5rem;
}
@media print {
  .Resume-row + .Resume-row {
    margin-top: 1rem;
  }
}
.Resume-column {
  flex: 1 0 0%;
}
@media print, (min-width: 1024px) {
  .Resume-column--right {
    order: 2;
    flex: 0 0 auto;
    margin-left: 2.5rem;
  }
}
.Resume-date {
  margin-bottom: 0.5em;
  font-size: 0.875rem;
  color: #475257;
}
.Resume-gridItem {
  flex: 1 0 0%;
  padding: 1rem;
}
.Resume-gridItem--2 {
  flex-basis: 50%;
}
@page {
  margin: 0;
}
@media print {
  html {
    height: 0;
    margin: 1cm 1.25cm;
    font-size: 13px;
  }
}
